* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.nav-list {
  height: 10vh;
  width: 50%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-left: auto;
  list-style: none;
}

.nav-links {
  text-decoration: none;
  font-size: 18px;
  color: black;
}

.home,
.about {
  position: absolute;
  width: 100%;
  height: 90vh;
}

.home-content,
.about-content {
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;

  img {
    height: 70%;
  }

  h1 {
    font-size: 50px;
    margin: 30px 0;
  }

  p {
    font-size: 20px;
  }
}

.home {
  background-image: linear-gradient(
    45deg,
    #ff9a9e 0%,
    #fad0c4 99%,
    #fad0c4 100%
  );
}

.about {
  background-image: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%);
}
